@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{--red: #e63946;--redDark: #a73040;--white: #f0f0f0;--gray: #3a3d40;--grayLight: #d0d2d3;--gray-light: #3a3d4022;--black: #181719;--blueLight: #45567d;--yellow: #c0ce41}*{margin:0;padding:0;box-sizing:border-box;scroll-behavior:smooth}body{color:#f0f0f0;color:var(--white);font-family:"Fira Code", monospace;-webkit-user-select:none;-ms-user-select:none;user-select:none;transition:0.2s}body ul,body li,body a{text-decoration:none;color:inherit;list-style:none}body input,body textarea{font-family:inherit;outline:none;border:1px solid rgba(51,51,51,0.3)}::-webkit-scrollbar{width:12px;background-color:#2d2e30}::-webkit-scrollbar-button{display:none}::-webkit-scrollbar-thumb{border:2px solid #2d2e30;margin:0 auto;background-color:#5f6368;border-radius:5px}::-webkit-scrollbar-track{width:10px}body header{--height-header: 50px;--width-header: 100%;--size-menu: 30px}body #contact a{--size-brand: 4rem;--gap-social: 1rem}header{position:fixed;z-index:1;height:var(--height-header);width:var(--width-header);display:flex;justify-content:flex-end;align-items:center;color:#f0f0f0;color:var(--white)}header .menu-dot{position:absolute;right:0;top:0;-webkit-transform:translate(-25%, 25%);transform:translate(-25%, 25%);width:var(--size-menu);height:var(--size-menu);border-radius:50%;box-shadow:2px 2px 5px #181719;box-shadow:2px 2px 5px var(--black);background-color:#3a3d40;background-color:var(--gray)}header .nav-dot{--p-left-right-nav-item: 0.4rem;cursor:pointer;position:relative;-webkit-transform:translate(-10%, calc(var(--size-menu) + 15px));transform:translate(-10%, calc(var(--size-menu) + 15px));-webkit-transform:translate(calc(100% + var(--p-left-right-nav-item) * 2), calc(var(--size-menu) + 15px));transform:translate(calc(100% + var(--p-left-right-nav-item) * 2), calc(var(--size-menu) + 15px));transition:-webkit-transform 0.2s;transition:transform 0.2s;transition:transform 0.2s, -webkit-transform 0.2s}header .nav-dot .nav-item{padding:0.2rem var(--p-left-right-nav-item);border-radius:5px;margin-top:0.1rem;background-color:#3a3d4022;background-color:var(--gray-light)}header .nav-dot .nav-item:hover{background-color:#3a3d40;background-color:var(--gray)}header .nav-dot.active{-webkit-transform:translate(-10%, calc(var(--size-menu) + 15px));transform:translate(-10%, calc(var(--size-menu) + 15px))}.hero{width:100%;height:100vh;display:flex;justify-content:flex-start;align-items:center;flex-direction:column;background-color:#3a3d40;background-color:var(--gray);background-position:center;background-size:cover;transition:background-image 1s linear}.hero>.name{--sizeName: 4rem;--sizeJob: 1rem;font-size:var(--sizeName);font-weight:normal;letter-spacing:0.5rem;color:#f0f0f0;color:var(--white);display:inline-block;position:relative}.hero>.name:after{content:attr(data-job);position:absolute;bottom:0;top:100%;-webkit-transform:translate(-100%, -1rem);transform:translate(-100%, -1rem);font-size:var(--sizeJob);font-weight:300;font-style:italic;letter-spacing:0.2rem;color:#e63946;color:var(--red)}.hero>.job{display:none;font-size:var(--sizeJob);font-weight:300;font-style:italic;letter-spacing:0.2rem;color:#e63946;color:var(--red)}@media (max-width: 412px){.hero{--sizeName: 2.5rem;--sizeJob: 1rem}}@media (max-width: 720px){.hero{--sizeName: 3rem;--sizeJob: 1rem}}.projects{--fontSize: 3rem;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;background-color:#45567d;background-color:var(--blueLight);text-align:center;padding:var(--fontSize) 2rem;display:flex;flex-direction:column;align-items:center;grid-gap:var(--fontSize);gap:var(--fontSize)}.projects .title{display:block;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;font-size:var(--fontSize);position:relative}.projects .title::after{content:"";position:absolute;height:2px;background-color:#f0f0f0;background-color:var(--white);bottom:0;left:-2rem;right:-2rem}.projects .grid{width:100%;display:grid;grid-gap:2rem;gap:2rem;grid-template-columns:repeat(auto-fit, minmax(320px, 1fr))}.projects .grid .project{box-shadow:1px 1px 3px #181719;box-shadow:1px 1px 3px var(--black);width:100%;height:100%;border-radius:5px;overflow:hidden}.projects .grid .project img{width:100%;height:calc(100% - 2rem * 2);object-fit:cover}.projects .grid .project .name{font-size:1.5rem;padding:1rem;background-color:#3a3d40;background-color:var(--gray);display:flex;align-items:center;justify-content:center}.projects .grid .project .name::before{content:"<  ";color:#c0ce41;color:var(--yellow);opacity:0;transition:opacity 0.2s}.projects .grid .project .name::after{content:"  />";color:#c0ce41;color:var(--yellow);opacity:0;transition:opacity 0.2s}.projects .grid .project .name:hover::before{opacity:1}.projects .grid .project .name:hover::after{opacity:1}.projects .show-all{border-radius:2px;padding:0.8rem 1rem;background-color:#3a3d40;background-color:var(--gray);display:flex;align-items:center;grid-gap:0.5rem;gap:0.5rem;transition:0.5s;font-weight:700;font-size:1.2rem}.projects .show-all:hover{background-color:#e63946;background-color:var(--red)}.projects .show-all i{font-weight:800;font-size:2rem;transition:0.2s}.projects .show-all:hover i{-webkit-transform:translate(0.2rem);transform:translate(0.2rem)}@media (max-width: 768px){.projects{--fontSize: 2.5rem}}@media (max-width: 375px){.projects{--fontSize: 1.5rem}}#contact{--fontSize: 3.5rem;background-color:#181719;background-color:var(--black);color:#d0d2d3;color:var(--grayLight);display:flex;flex-direction:column;align-items:center;justify-content:center;grid-gap:3rem;gap:3rem;padding:130px 0}#contact .description{text-align:center}#contact .description h2{font-size:var(--fontSize);font-weight:800;letter-spacing:2px}#contact .description p{margin-top:1rem;font-style:italic}#contact .contact-list{display:flex;grid-gap:2rem;gap:2rem}#contact .contact-list .contact-item{display:flex}#contact .contact-list .contact-item a{display:flex;align-items:center;font-size:1.5rem;grid-gap:var(--gap-social);gap:var(--gap-social);font-family:"roboto";text-transform:uppercase;transition:all 0.2s}#contact .contact-list .contact-item a span{display:block;order:1}#contact .contact-list .contact-item a img{display:block;order:0;width:var(--size-brand);height:var(--size-brand);object-fit:cover}@media (max-width: 380px){#contact{padding:30px;--fontSize: 1.5rem}#contact .description h2{font-size:1.5rem}}@media (max-width: 768px){#contact{--fontSize: 1.5rem}#contact .contact-list .contact-item a span{display:none}#contact .contact-list .contact-item a i{font-size:3rem}}@media (max-width: 720px){#contact{--fontSize: 3.5rem;background-color:#181719;background-color:var(--black);color:#d0d2d3;color:var(--grayLight);display:flex;flex-direction:column;align-items:center;justify-content:center;grid-gap:3rem;gap:3rem;padding:130px 0}#contact .contact-list{display:flex;grid-gap:2rem;gap:2rem}#contact .contact-list .contact-item{display:flex}#contact .contact-list .contact-item a{--size-brand: 3rem;display:flex;align-items:center;font-size:1.5rem;grid-gap:0.4rem;gap:0.4rem;font-family:"roboto"}#contact .contact-list .contact-item a span{display:none;order:1}#contact .contact-list .contact-item a img{display:block;order:0;width:var(--size-brand);height:var(--size-brand);object-fit:cover}}footer{background-color:#181719;background-color:var(--black);display:flex;justify-content:space-between;padding:2.2rem 8rem;font-size:1.2rem;color:#d0d2d3;color:var(--grayLight);border-top:0.25rem solid #e63946;border-top:0.25rem solid var(--red)}@media (max-width: 768px){footer{justify-content:center;align-items:center;padding:1rem}footer span:first-child{display:none}}

